.container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    background-color: rgb(29, 28, 28);

    .sidebar {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 15%;
        max-height: 100%;
        background-color: $white-mode;
        margin: 1rem 0 1rem 1rem;
        border-radius: 10px;
        box-shadow: 0 0 1em $grey-color;
        &.dark {
            background-color: $dark-mode;
        }
    }
    .title-content-container {
        display: flex;
        flex-direction: column;
        height: 100%;
        width: 100%;
        text-align: center;
        .title {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            background-color: $white-mode;
            margin: 1rem;
            max-height: 8vh;
            min-height: 8vh;
            max-width: 100%;
            text-transform: uppercase;
            border-radius: 10px;
            box-shadow: 0 0 1em $grey-color;
            &.dark {
                background-color: $dark-mode;
                color: $white-mode;
            }
            .search {
                width: 90%;
                height: 90%;
                border: none;
                text-align: center;
                border-radius: 10px;
                box-shadow: none;
                font-size: 40px;
                &:focus {
                    outline: none;
                }
                &.dark {
                    background-color: $dark-mode;
                    color: $white-mode;
                }
            }
        }
        .content {
            display: flex;
            background-color: $white-mode;
            margin: 0 1rem 1rem 1rem;
            height: 100%;
            overflow: auto;
            justify-content: center;
            border-radius: 10px;
            box-shadow: 0 0 1em $grey-color;
            &.dark {
                background-color: $dark-mode;
            }
        }
    }
}
